import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Amparo from '../views/Amparo.vue'
import Civil from '../views/Civil.vue'
import Familiar from '../views/Familiar.vue'
import Fiscal from '../views/Fiscal.vue'
import Laboral from '../views/Laboral.vue'
import Mercantil from '../views/Mercantil.vue'
import Penal from '../views/Penal.vue'
import SS from '../views/SS.vue'
import Pension from '../views/Pension.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/About',
    name: 'About',
    component: About
  },
  {
    path: '/Amparo',
    name: 'Amparo',
    component: Amparo
  },
  {
    path: '/Civil',
    name: 'Civil',
    component: Civil
  },
  {
    path: '/Familiar',
    name: 'Familiar',
    component: Familiar
  },
  {
    path: '/Fiscal',
    name: 'Fiscal',
    component: Fiscal
  },
  {
    path: '/Laboral',
    name: 'Laboral',
    component: Laboral
  },
  {
    path: '/Mercantil',
    name: 'Mercantil',
    component: Mercantil
  },
  {
    path: '/Penal',
    name: 'Penal',
    component: Penal
  },
  {
    path: '/SS',
    name: 'SS',
    component: SS
  },
  {
    path: '/Pension',
    name: 'Pension',
    component: Pension
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
